$number-size: 30px;

.numbers {
  display: flex;
  list-style-type: none;
  flex-wrap: wrap;

  a, a:visited {
    color: #fff;
    text-decoration: none;
  }
}

.number {
  display: block;
  width: $number-size;
  height: $number-size;
  line-height: $number-size;
  text-align: center;
  margin-right: 2px;
}

.pagination {
  display: flex;
  justify-content: space-between;
}

.finish {
  text-align: center;
}
