$header-height: 50px;
$footer-height: 50px;

.container {
  flex-direction: column;
}

.header {
  height: $header-height;
}

.content {
  padding: 0;
  height: 100vh;

  &.withHeaderAndFooter {
    height: calc(100vh - #{$header-height + $footer-height});
    overflow: scroll;
  }
}

.footer {
  display: flex;
  justify-content: space-around;
  height: $footer-height;
  padding-top: 8px;
}
