.circleBg {
  fill: none;
  stroke: #eee;
  stroke-width: 3.8;
}

.circle {
  fill: none;
  stroke-width: 2.8;
  stroke-linecap: round;
}

.percentage {
  fill: #666;
  font-family: sans-serif;
  font-size: 0.4em;
  text-anchor: middle;
}

.title {
  fill: #666;
  font-family: sans-serif;
  font-size: 0.25em;
  text-anchor: middle;
}

.green {
  stroke: #32b643;
}

.yellow {
  stroke: #ffb700;
}

.red {
  stroke: #e85600;
}

